/* ************************ */

/* FONT STYLING ***********************  */

@import url('https://fonts.googleapis.com/css?family=Lora:400,700|Roboto:400,700&display=swap');


/* 
font-family: 'Roboto', sans-serif;

font-family: 'Lora', serif;
 */


p, h2 {
  margin: 0;
  padding: 0;
}

p, label, button {
  font-family: 'Roboto', sans-serif;
}

h2 {
  font-family: 'Lora', serif;
  font-weight: 700;
}

.primaryProductInfoContainer h2 {
  font-size: 30px
}

.productInfoContainer h2 {
  font-size: 24px
}

.productBrewer {
  font-family: 'Roboto', sans-serif;
  color: #9E9A9F;
  font-size: 16px;
}

.productType {
  font-family: 'Roboto', sans-serif;
  color: #6F6A71;
  font-size: 14px;
}

.productSizeandABV {
  font-family: 'Roboto', sans-serif;
  color: #9E9A9F;
  font-size: 12px;
}

.productCurrency {
  font-family: 'Roboto', sans-serif;
  color: #6F6A71;
  font-size: 11px;
}

.productPrice {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  color: #423C44;
  font-size: 16px;
}

/* UNIVERSAL ***********************  */

.wrapper {
  width: 60%;
  margin: 0 auto;
}

.body {
  background: #F5F8FB;
}
/* HEADER ***********************  */

.headerContainer {
  background: #FFFFFF;
  border:  1px solid #DDE4EF;
  box-shadow: 0px 0.5px 5px rgba(0, 0, 0, 0.15);
}

.headerInfo {
  position: relative;
  display: flex;
  align-items: center;
}

.filterBtn {
  margin: 32px 0;
  background: white;
  border: none;
  border-radius: 10px;
}
.filterBtn img {
  margin-top: 5px;
}

.filterBtn:hover,
.filterBtn:focus {
  background:  #ffff00;
}

.logo img{
  position: absolute;
  width: 90px; 
  left: 50%; 
  margin-left: -37px; 
  top: 50%; 
  margin-top: -33px;
}

/* PRODUCT DISPLAY ***********************  */

.productDisplayContainer{
  margin-top:20px;
}

form {
  display: flex;
  justify-content: space-between;
}

.sortButton {
  border: none;
  box-shadow: 2px 2px 1px black;
  border-radius: 5px;
}

.sortButton:hover,
.sortButton:focus {
  background: #ffff00
} 

.sortButton:active {
  box-shadow: 0px 0px 1px black;
}

.sortInputContainer {
  margin: 0 5px;
}

/* ****************************** Card styles ****************************** */


.productCard{
  background: #FFFFFF;
  border:  1px solid #DDE4EF;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.05);
  margin: 11px 0;
  padding: 30px 20px;
}

.productImageContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.productInfoContainer {
  width: 100%;
}

.productInfoContainer .basicProductInfo {
  text-align: center;
}

.basicProductInfo {
  height: 107.167px;
  display: flex;
  flex-direction: column; 
  justify-content: end; 
  border-bottom: 1px solid rgb(243,241,243);
}

.otherProductInfo {
  display: flex;
  justify-content: space-between;
}

.sizeAndABVProductInfo {
  margin-top: 10px
}

h2 {
  margin-bottom: 5px;
}

.productBrewer {
  margin-bottom: 20px;
}

/* Tertiary Product styles ****************************** */

.tertiaryProductCardContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

}
.tertiaryProductCard{
  width: calc(33% - 12px);
}

.tertiaryProductCard img{
  height: 150px;
}


/* Secondary Product styles ****************************** */


.secondaryProductCardContainer{
  display: flex;
  justify-content: space-between;
}

.secondaryProductCard{
  width: calc(50% - 10px);
  padding: 30px 40px;
}

.secondaryProductCard img {
  height: 200px;
}

/* PRIMARY Product styles ****************************** */

.primaryProductCard {
  width: 100%;
  padding: 40px 0;
  display: flex;
}

.primaryProductImageContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 3%;
  margin-right: 1.5%;
}

.primaryProductImageContainer img {
  object-fit: contain;
  width: 175px;
  height: 210px;
}
.primaryProductInfoContainer {
  width: 67%;
  padding: 10px 0;
}
.primaryDetailedProductInfo, 
.detailedProductInfo {
  margin-top: 16px;}

.primaryDetailedProductInfo{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px
}

.primaryOtherProductInfo .productSizeandABV {
  margin-top: 10px;
}

/* MEDIA QUERIES styles ****************************** */

@media (max-width: 1150px) {
  .primaryProductInfoContainer {
    width: 100%;
    padding: 30px 40px;
  }
  
  .primaryProductInfoContainer .basicProductInfo {
    text-align: center;
  }
  
  .primaryProductCard,
  .secondaryProductCardContainer {
    flex-direction: column;
  }

  .primaryProductCard {
    align-items: center;
  }

  .secondaryProductCard {
    width: 100%;
  }

  .tertiaryProductCard {
    width: calc(50% - 12px);
  }
}

@media (max-width: 950px) {

  .wrapper, 
  .tertiaryProductCard {
    width: 100%;
  }
  .filterBtn {
    margin-left: 30px;
  }
}